@import url('https://fonts.googleapis.com/css?family=Open+Sans|Mirza|Lato');

.App {
  text-align: center;
  /*font-family: 'Lato', sans-serif;*/
  font-family: 'Open Sans', sans-serif;
}


/** HEADER **/


.photo {
  -webkit-animation: photo-spin infinite 20s linear;
          animation: photo-spin infinite 20s linear;
  height: 80px;
}

@-webkit-keyframes photo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes photo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  
  background-color: #011627;
  height: 300px;
  padding: 20px;
  color: #F2F4F3;
}

.header-text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;

  text-align: right;
  line-height: 0.1em;
}

.title { /* Name */
  font-family: 'Mirza', cursive;
  font-size: 3em;
  letter-spacing: 3px;
}

.subtitle { /* Name subtitle */
  font-size: 1.3em;
  letter-spacing: 5px;
}

.header-logo { /* Photo */
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
}


/** CONNECTION BUTTONS **/


.connections {
  margin: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.connection {
  display: inline-block;
  height: 100px;
  width: 100px;
  text-align: center;
  /*padding: 20px;*/
  margin: 20px;

  border: 5px solid white;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 3px grey;
          box-shadow: 0 0 3px grey;
  background-color: grey;
}

.connection-text {
  display: inline-block;
  /*vertical-align: middle;*/
  width: 100%;
  padding-top: 50%;
  padding-bottom: 50%;
  margin-top:-0.6em;
}

/*
 * -- TABLET & BELOW MEDIA QUERIES --
 */
 
@media (max-width: 64em) {
    
}

/*
 * -- MOBILE & BELOW MEDIA QUERIES --
 */
 
@media (max-width: 48em) {
    .header {
      -ms-flex-direction: column;
          flex-direction: column;
    }

    .header-text {
      text-align: center;
    }
}



